@keyframes float-up {
  0% {
      opacity: 0;
      top: 4rem;
  }
  100% {
      opacity: 1;
      top: 0;
  }
}

.float-up {
  opacity: 0;
  position: relative;
  animation-name: float-up;
  animation-duration: 0.45s;
  animation-fill-mode: forwards;
}

.center_content {
  position: fixed;
  left: 50%; /*move the top left point of block to the center*/
  top: 50%;  /*move the top left point of block to the center*/
  transform: translate(-50%, -50%); /*move the center of the block to center*/
  width: 600px;
  height: 450px;
}
